import React from "react";
import PropTypes from "prop-types";
import { Accordion } from "react-bootstrap";
import FaqList from "../../components/faq";
import data from "../../data/seo-package/data.json";

export default function SEOPackage() {
    return (
        <>
            <div className="seo-container container">
                <div>
                    <div className="seo-package-inner d-flex flex-wrap flex-row">
                        <div className="seo-pachage-content">
                            <h2 className="seo-subtitle">{data.title}</h2>
                            {data.content.map((content, i) => {
                                return <p key={i}>{content.description}</p>;
                            })}
                        </div>
                        <div className="seo-packages-img"></div>
                    </div>

                    <div className="organic-seo-section d-flex flex-wrap flex-row">
                        <div className="organic-seo-packages-img"></div>

                        <div className="organic-seo-inner-section">
                            <h5> {data.subtitle} </h5>
                            <h2 className="seo-subtitle">
                                {data.section2title}
                            </h2>
                            {data.section2content.map((content2, i) => {
                                return <p key={i}>{content2.description}</p>;
                            })}
                        </div>
                    </div>

                    <div className="package-section">
                        <div className="container">
                            <h4 className="seo-page-title">
                                {data.section3title}
                                <span>{data.section3span}</span>
                            </h4>
                            {data.accordion.map((accordionItem, i) => {
                                return (
                                    <Accordion
                                        key={i}
                                        defaultactivekey={i}
                                        className="accordion-item"
                                    >
                                        <FaqList
                                            title={accordionItem.accordionTitle}
                                            description={
                                                accordionItem.accordionDescription
                                            }
                                        />
                                    </Accordion>
                                );
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

SEOPackage.propTypes = {
    subtitle: PropTypes.string,
    title: PropTypes.string,
    description: PropTypes.string,
    item: PropTypes.string,
    section2title: PropTypes.string,
    section3title: PropTypes.string,
    section3span: PropTypes.string,
    accordionTitle: PropTypes.string,
    accordionDescription: PropTypes.string,
};
